.boxSigno{
    background-color: #320994;
    color: aliceblue;
    margin: 25px 10px 0px;
    padding: 5px 0px;
    display: flex;
    flex-direction: column;
    align-items:center ;
    justify-content: space-between;
    box-sizing: border-box;
    border-radius: 5px  ;
    border: 1px solid #111111;
    width: 150px;
    height: 200px;

}
.imagemSigno{
    width: 140px;
    height: 100px;
    background-color: aliceblue;
    object-fit: contain;
    border-radius: 2px;
    border: 1px solid #27da9e;
    box-sizing: border-box;
    margin-top: 10px;
    padding: 8px;
    flex: 1;
}
.nomeSigno{
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 10px;

}
.periodoNascimento{
    font-size: 14px;
}